* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    user-select: none;
    outline: none;
}

body {
    display: flex;
    justify-content: center;
    padding: 100px;
    height: 100vh;
    font-family: Arial, Helvetica, sans-serif;
    color: #222;
    background-color: rgb(149, 20, 44);
}