.contenedor-formulario {
    display: flex;
    flex-wrap: nowrap;
    width: 200px;
    height: 40px;
    margin: 5px 0;
    box-shadow: 0 0 10px rgba(76, 7, 7, 0.585)
}

.formulario {
    width: 80%;
    height: 100%;
    padding: 5px;
    border-radius: 5px 0 0 5px;
    border: none;
}

.boton-enviar-formulario {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    border: none;
}



.tareas-contenedor {
    display: flex;
    flex-direction: column;
    gap: 5px
}